@import url(https://fonts.googleapis.com/css?family=Anton);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
    --primary: #e66e0b;
    --secondary: #473d72;
    --light: #fafafd;
  }
  
  html {
    box-sizing: border-box;
  }
  
  *,
  *:before,
  *:after {
    box-sizing: inherit;
  }
  
  ::-moz-selection {
    background-color: #e66e0b;
    background-color: var(--primary);
    color: #fafafd;
    color: var(--light);
  }
  
  ::selection {
    background-color: #e66e0b;
    background-color: var(--primary);
    color: #fafafd;
    color: var(--light);
  }
  
  
  
  input {
    width: 2rem;
    height: 1.75rem;
    margin: 0 0.75rem;
    background-color: transparent;
    border: 1px solid #473d72;
    border: 1px solid var(--secondary);
    border-radius: 50%;
    text-align: center;
  }
  
  .menu {
    padding: 2rem 2.25rem 1rem;
    margin: 0 auto;
    max-width: 50rem;
    background-color:#ffebad;
    box-shadow: 0 0 1rem rgba(0, 0, 0, 0.1);
    
  }
  
  @media (min-width: 660px) {
    .menu {
      display: grid;
      grid-gap: 4rem;
      grid-template-columns: 1fr 1fr;
    }
  }
  
  .menu-item {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 1.5rem;
  }
  
  
  .mains {
    grid-column: 1 / 2;
  }
  
  .mains-name {
    margin: 0;
    flex-grow: 1;
    color:#e66e0b;
    color:var(--primary)
  }
  
  .mains-description {
    flex: 1 1 100%;
    margin: 0.75rem 0 0;
    color: #473d72;
    color: var(--secondary);
  }
  
  .aside {
    grid-column: 2 / 3;
  }
  
  
  .extras + .extras {
    margin-top: 1.5rem;
  }
  
  @media (min-width: 660px) {
    .extras + .extras {
      margin-top: 2.5rem;
    }
  }
  
  .extras .menu-item {
    margin-bottom: 0.75rem;
  }
  
  .extras-heading {
    margin: 0 0 1.5rem;
    border-bottom: 1px solid #473d72;
    border-bottom: 1px solid var(--secondary);
    color: #e66e0b;
    color: var(--primary);
  }
  
  .extras-name {
    flex-grow: 1;
    color:#473d72;
    color:var(--secondary);
  }
  
  .total {
    grid-column: 1 / 3;
    margin-top: 2.5rem;
    padding: 0.75rem 0 1.25rem;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    position: -webkit-sticky;
    position: sticky;
    bottom: 0;
    color:#e66e0b;
    color:var(--primary);
    background-color: #fafafd;
    background-color: var(--light);
    border-top: 1px solid #473d72;
    border-top: 1px solid var(--secondary);
    box-shadow: 0 -0.5rem 0.75rem 0.5rem #fafafd;
    box-shadow: 0 -0.5rem 0.75rem 0.5rem var(--light);
  }
  
  @media (min-width: 660px) {
    .total {
      margin-top: 0;
    }
  }
  
  .total-title {
    margin: 0;
  }
  
  .total-price {
    font-size: 1.5rem;
  }
  
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;

}



.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.negrita {
  font-weight: bold;
}

.navbar{
  font-style: italic;
}

.h2{
  color:#ea8118;
}

